@import 'scss/libs/variables.scss';

.logo {
	display: block;
}

.icon {
	margin-right: 20px;

	@media (max-width: 992px) {
		width: 40px;
		height: 40px;
	}

	@media (max-width: 767px) {
		width: 35px;
		height: 35px;
		margin-right: 5px;
	}

	@media (max-width: 480px) {
		display: none;
	}
}